	onLoad:function(e){
		_self=this;
		_self.id=e.id
		console.log(_self.id)
		this.getContent(_self.id);
	},
	methods: {

		onClickDownload:function(path){
			console.log(path)
			const downloadTask = uni.downloadFile({
				url: path, //仅为示例，并非真实的资源
				success: function (res) {
				    if (res.statusCode === 200) {
				        console.log('下载成功');
				    }
				},
				fail:function(){
					console.log('下载失败');
				}
			});
			downloadTask.onProgressUpdate(function (res) {
			    console.log('下载进度' + res.progress);
			    console.log('已经下载的数据长度' + res.totalBytesWritten);
			    console.log('预期需要下载的数据总长度' + res.totalBytesExpectedToWrite);
			 
			   
			});
		},
		getContent:function(id){
			uni.request({
				header:{
					'sessionId': 'fa2e5c5698b0480eb68ec05fa9a35a3d' //自定义请求头信息
				},
				url:'http://192.168.1.200:40881/bulletin/queryBulletinDetail?id=1157',
				success:function(res){
					const data= res.data.data[0];
					_self.pubAttachmentList=data.pubAttachmentList;
					console.log(data)
					_self.contents=data.systemBulletin;
					_self.starttime=friendlyDate(new Date(data.systemBulletin.createtime).getTime());
					
				},
				fail:function(){
					
				}
			})
		}
	}
}
